body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.checked {
  -webkit-text-decoration: line-through black;
          text-decoration: line-through black; 
}

input{
  font-size:2.5em;
  background:content-box -webkit-linear-gradient(right, white,rgb(255, 255, 120));
  background:content-box linear-gradient(to left, white,rgb(255, 255, 120));
  /* padding: 10px; */
  border: none;
  /* margin: 20px; */
  border-radius: 20px;
  outline: none;
}
.button{
  font-size: 30px;
  font-family:'Love Ya Like A Sister', cursive;
  color: black;
  border:none;
  background: transparent;
  outline: none;
}

label{
  font-size: 30px;
  
}

body {
  background-image: url(https://www.balancedancestudios.com/wp-content/uploads/2016/03/cork.jpg); 
  /* background-image: url(https://c.pxhere.com/photos/a1/bd/backdrop_background_blank_board_brown_bulletin_business_clipboard-1141010.jpg!d);  */
  /* background: content-box linear-gradient(to right,white,rgb(249, 249, 130)); */
  font-family:'Love Ya Like A Sister', cursive;
}

ul{
  list-style: none;
  /* text-align:center; */
  /* align-items: center; */
  font-size: 30px;
  /* margin-left: 35%; */
